<template>
    <div>
        <el-table :header-cell-style="{'text-align':'center'}" ref="singleTable" :data="list" highlight-current-row>
            <el-table-column type="index" width="50"></el-table-column>
            <el-table-column property="CarNumber" label="车牌号" width="120" align="center"></el-table-column>
            <el-table-column property="MaxFangLiang" label="最大方量" width="120" align="center"></el-table-column>
            <el-table-column property="belong" label="所属车队" align="center"></el-table-column>
        </el-table>
    </div>
</template>
    
<script>
export default {
    name: 'carInfo',
    data() {
        return {
            list: []
        }
    },
    mounted(){
        this.$http.get('/carInfos')
            .then((res)=> {
                this.list=[]
                res.data.forEach(element => {
                    this.list.push(element);
                });
            })
            .catch((err) =>{
                console.log(err)
            })
    }
}
</script>
    
<style scoped>
*{
    margin: 0;
    padding: 0;
}
.mainRightContentRoot {
    width: 1000px;
    height: 1000px;
}

.mainRightContentRoot .table {
    width: 100%;
}

