<template>
    <div class="transInfoRoot">
        <el-dialog title="货运信息" :visible.sync="dialogFormVisible">
            <el-form :inline="true" label-width="80px" :model="selctedItemUpdate">
                <el-form-item label="车牌号">
                    <el-input v-model="selctedItemUpdate.CarNumber"></el-input>
                </el-form-item>
                <el-form-item label="用户名">
                    <el-input v-model="selctedItemUpdate.UserName"></el-input>
                </el-form-item>
                <el-form-item label="姓名">
                    <el-input v-model="selctedItemUpdate.RealName"></el-input>
                </el-form-item>
                <el-form-item label="车次">
                    <el-input v-model="selctedItemUpdate.CarCount"></el-input>
                </el-form-item>
                <el-form-item label="起运地">
                    <el-input v-model="selctedItemUpdate.StartLocation"></el-input>
                </el-form-item>
                <el-form-item label="目的地">
                    <el-input v-model="selctedItemUpdate.DestinatLocation"></el-input>
                </el-form-item>
                <el-form-item label="方量">
                    <el-input v-model="selctedItemUpdate.RealFangliang"></el-input>
                </el-form-item>
                <el-form-item label="出站时间">
                    <el-input v-model="selctedItemUpdate.leaveStationTime"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit">修改</el-button>
                    <el-button type="primary" @click="onCancle">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>



        <el-dialog title="筛选" :visible.sync="dialogFilterVisible">
            <el-form :inline="true" label-width="80px" :model="filterData">
                <el-form-item label="姓名">
                    <el-input v-model="filterData.RealName"></el-input>
                </el-form-item>
                <el-form-item label="日期">
                    <el-input v-model="filterData.startDay"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmitFilter">确定</el-button>
                    <el-button type="primary" @click="onCancleFilter">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>



        <div class="pageContainer">
            <el-pagination background layout="total, sizes, prev, pager, next" :current-page="tablePage.pageNum"
                :page-size="tablePage.pageSize" :page-sizes="pageSizes" :total="tablePage.total"
                @size-change="handleSizeChange" @current-change="handlePageChange" />
            <el-button type="primary" @click="filterDefined">筛选</el-button>
        </div>



        <!-- :default-sort="{ prop: 'leaveStationTime', order: 'ascending' }" -->
        <el-table show-summary :summary-method="getSummaries" stripe class="elTable"
            :header-cell-style="{ 'text-align': 'center' }" :row-class-name="tableRowClassName" ref="singleTable"
            :data="list" highlight-current-row>

            <el-table-column fixed="left" label="操作" width="100">
                <template slot-scope="scope">
                    <el-button @click="editorItem(scope.row)" type="text" size="small">编辑</el-button>
                    <el-button @click="deleteCurrentItem(scope.row)" type="text" size="small">删除</el-button>
                </template>
            </el-table-column>


            <el-table-column property="CarNumber" label="车牌号" width="120" align="center"></el-table-column>
            <el-table-column property="UserName" label="用户名" width="120" align="center"></el-table-column>
            <el-table-column property="RealName" label="姓名" align="center"></el-table-column>
            <el-table-column property="CarCount" label="车次" width="120" align="center"></el-table-column>
            <el-table-column property="StartLocation" label="起运地" align="center"></el-table-column>
            <el-table-column property="DestinatLocation" label="目的地" align="center"></el-table-column>
            <el-table-column property="RealFangliang" label="方量" width="120" align="center"></el-table-column>

            <el-table-column property="startDay" label="日期" width="120" align="center"></el-table-column>

            <el-table-column property="leaveStationTime" label="出站时间" width="200" align="center">
            </el-table-column>
            <el-table-column property="sayStationTime" label="报站时间" width="200" align="center"></el-table-column>

            <el-table-column property="arrivalTime" label="到工地时间" width="200" align="center"></el-table-column>

            <el-table-column property="startOffTime" label="卸货时间" width="200" align="center"></el-table-column>

            <el-table-column property="offCompleteTime" label="卸完时间" width="200" align="center"></el-table-column>

            <el-table-column property="backTime" label="回站时间" width="200" align="center"></el-table-column>

        </el-table>

    </div>
</template>
    
<script>
export default {
    name: 'transInfo',
    data() {
        return {
            dialogFilterVisible: false,
            filterData: {
                startDay: '',
                RealName: ''
            },


            oragionList: [],
            list: [],
            dialogFormVisible: false,
            currentSeleted: {},
            selctedItemUpdate: {

            },
            names: [],
            days: [],
            tablePage: {
                pageNum: 1, // 第几页
                pageSize: 10, // 每页多少条
                total: 0 // 总记录数
            },
            pageSizes: [10, 20, 30]
        }
    },
    methods: {
        filterDefined() {
            this.dialogFilterVisible = true;
        },
        onSubmitFilter() {
            this.dialogFilterVisible = false;
            this.list = this.getPageList(this.oragionList, this.tablePage.pageNum, this.tablePage.pageSize)
        },
        onCancleFilter() {
            this.dialogFilterVisible = false;
            // this.filterData.RealName = ''
            // this.filterData.startDay = ''
        },
        /**
     * 分页数据处理
     * @param data [Array] 需要分页的数据
     * @param num [Number] 当前第几页
     * @param size [Number] 每页显示多少条
*/
        getPageList(data, num, size) {

            //先进行筛选，再进行分页
            let result = data


            if (this.filterData.RealName != '') {
                result = result.filter(item => {
                    if (item.RealName === this.filterData.RealName)
                        return true;
                })
            }


            if (this.filterData.startDay != '') {
                result = result.filter(item => {
                    if (item.startDay.toString() === this.filterData.startDay.toString())
                        return true;
                })
            }

            this.tablePage.total = result.length;


            let list, total, start, end, isFirst, isLast
            total = result.length
            isFirst = total < size
            isLast = Math.ceil(total / size) === num
            start = (num - 1) * size
            end = isFirst || isLast ? start + (total % size) : start + size
            list = result.slice(start, end)
            list.forEach((item, index) => {
                item.seq = index + start
            })
            return list
        },
        handlePageChange(currentPage) {
            this.tablePage.pageNum = currentPage
            // 在此刷新数据
            this.list = this.getPageList(this.oragionList, this.tablePage.pageNum, this.tablePage.pageSize)
        },
        handleSizeChange(pageSize) {
            this.tablePage.pageSize = pageSize
            // 在此刷新数据
            this.list = this.getPageList(this.oragionList, this.tablePage.pageNum, this.tablePage.pageSize)
        },
        getSummaries(param) {
            const { columns, data } = param;
            const sums = [];
            columns.forEach((column, index) => {
                if (index === 0) {
                    sums[index] = '合计';
                    return;
                }
                if (index == 1) {
                    const values = data.map(item => Number(item[column.property]));
                    sums[index] = values.length;
                    sums[index] += ' 车'
                }
                if (index == 7) {
                    const values = data.map(item => Number(item[column.property]));
                    if (!values.every(value => isNaN(value))) {
                        sums[index] = values.reduce((prev, curr) => {
                            const value = Number(curr);
                            if (!isNaN(value)) {
                                return prev + curr;
                            } else {
                                return prev;
                            }
                        }, 0);
                        sums[index] += '  方';
                    } else {
                        sums[index] = '';
                    }
                }

            });

            return sums;
        },
        filterHandler(value, row, column) {
            const property = column['property'];
            return row[property] === value;
        },
        tableRowClassName({ row, rowIndex }) {
            if (rowIndex === 1) {
                return 'warning-row';
            } else if (rowIndex === 3) {
                return 'success-row';
            }
            return '';
        },
        //修改选中内容
        onSubmit() {
            this.dialogFormVisible = false;

            let stateInfo = {
                collectionName: 'transInfo',
                where: {
                    CarNumber: this.currentSeleted.CarNumber,
                    UserName: this.currentSeleted.UserName,
                    RealName: this.currentSeleted.RealName,
                    CarCount: this.currentSeleted.CarCount,
                    StartLocation: this.currentSeleted.StartLocation,
                    DestinatLocation: this.currentSeleted.DestinatLocation,
                    RealFangliang: this.currentSeleted.RealFangliang,
                    leaveStationTime: this.currentSeleted.leaveStationTime
                },
                content: {
                    $set: {
                        CarNumber: this.selctedItemUpdate.CarNumber,
                        UserName: this.selctedItemUpdate.UserName,
                        RealName: this.selctedItemUpdate.RealName,
                        CarCount: this.selctedItemUpdate.CarCount,
                        StartLocation: this.selctedItemUpdate.StartLocation,
                        DestinatLocation: this.selctedItemUpdate.DestinatLocation,
                        RealFangliang: this.selctedItemUpdate.RealFangliang,
                        leaveStationTime: this.selctedItemUpdate.leaveStationTime
                    }
                }
            };

            this.$http.post("/updateInfo", stateInfo).then((scucess) => {
                this.$message({
                    message: '修改成功！',
                    type: 'success'
                });
                this.getList();
            }, (fail) => {
                console.log(fail);
            });

        },
        onCancle() {
            this.dialogFormVisible = false;
        },
        deleteItem(row) {
            let where = {
                CarNumber: row.CarNumber,
                UserName: row.UserName,
                leaveStationTime: row.leaveStationTime
            }

            this.$http.get(`/deleteTransInfos?where=${JSON.stringify(where)}`).then(
                (scucess) => {
                    switch (scucess.data) {
                        case "where-undefined":
                            console.log("where-undefined");
                            break;
                        case "success":
                            this.$message({
                                type: 'success',
                                message: '删除成功!'
                            });
                            this.getList();
                            break;
                        case "dataBase-error":
                            console.log("dataBase-error");
                            break;
                        case "server-undefinedError":
                            console.log("server-undefinedError");
                            break;
                    }
                },
                (fail) => {
                    console.log("网络请求超时！");
                }
            );
        },
        editorItem(row) {
            this.currentSeleted = row;
            this.selctedItemUpdate = JSON.parse(JSON.stringify(row))
            this.dialogFormVisible = true;
        },
        deleteCurrentItem(row) {

            this.$confirm(`此操作将永久删除该记录, 是否继续?-${row.UserName}-${row.RealName}-${row.CarNumber}`, '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                this.deleteItem(row);
            }).catch(() => {
                this.$message({
                    type: 'info',
                    message: '已取消删除'
                });
            });


        },
        getList() {
            this.$http.get('/transInfos')
                .then((res) => {
                    this.names = []
                    this.days = [];
                    this.list = []
                    this.oragionList = []
                    let data = res.data;

                    //按出站时间排序
                    let sortData = data.sort((p1, p2) => {
                        if (p1.leaveStationTime === undefined) return 1;
                        if (p2.leaveStationTime === undefined) return -1;
                        return Date.parse(p1.leaveStationTime) - Date.parse(p2.leaveStationTime)
                    })

                    //按姓名分组
                    let map = new Map();
                    let tempDays = []
                    sortData.forEach(item => {

                        if (!tempDays.includes(item.startDay)) {
                            tempDays.push(item.startDay)
                        }

                        if (map.has(item.RealName)) {
                            let items = map.get(item.RealName);
                            items.push(item);
                        } else {
                            let items = []
                            items.push(item)
                            map.set(item.RealName, items);
                            this.names.push({
                                text: item.RealName,
                                value: item.RealName
                            })
                        }
                    })
                    map.forEach(key => {
                        key.forEach(item => {
                            this.oragionList.push(item)
                        })
                    })
                    this.tablePage.total = this.oragionList.length;
                    this.list = this.getPageList(this.oragionList, this.tablePage.pageNum, this.tablePage.pageSize)

                    tempDays.forEach(item => {
                        this.days.push({
                            text: item,
                            value: item
                        })
                    })

                    this.setDefaultFilter();

                })
                .catch((err) => {
                    console.log(err)
                })
        },
        setDefaultFilter() {
            //this.$refs.singleTable.columns[8].filteredValue=[this.days[this.days.length-1].value]
        }
    },
    mounted() {
        this.getList();
    }
}
</script>
    
<style scoped>
