import VueRouter from 'vue-router'
import User from "./user";
import CarInfo from "./carInfo";
import DestinateLocationInfo from "./destinateLocationInfo";
import StartLocationInfo from "./startLocationInfo";
import TransInfo from "./transInfo";
import TransState from "./transState";
import DicInfo from "./dicInfo";
import OperationInfo from "./operationInfo";

export default new VueRouter({
    routes:[{
        name:'user',
        path:'/user',
        component:User
    },
    {
        name:'carInfo',
        path:'/carInfo',
        component:CarInfo
    },
    {
        name:'destinateLocationInfo',
        path:'/destinateLocationInfo',
        component:DestinateLocationInfo
    },
    {
        name:'startLocationInfo',
        path:'/startLocationInfo',
        component:StartLocationInfo
    },
    {
        name:'transInfo',
        path:'/transInfo',
        component:TransInfo
    },
    {
        name:'transState',
        path:'/transState',
        component:TransState
    },
    {
        name:'dicInfo',
        path:'/dicInfo',
        component:DicInfo
    },
    {
        name:'operationInfo',
        path:'/operationInfo',
        component:OperationInfo
    }
]
})