<template>
    <div>
        <el-table :header-cell-style="{ 'text-align': 'center' }" ref="singleTable" :data="list" highlight-current-row>
            <el-table-column type="index" width="50"></el-table-column>
            <el-table-column property="UserName" label="用户名" width="120" align="center"></el-table-column>
            <el-table-column property="Password" label="密码" width="120" align="center"></el-table-column>
            <el-table-column property="CarNumber" label="车牌号" align="center" width="120"></el-table-column>
            <el-table-column property="RealName" label="真实姓名" width="200" align="center"></el-table-column>
            <el-table-column property="IDNumber" label="身份证" width="200" align="center"></el-table-column>
        </el-table>
    </div>
</template>
    
<script>
export default {
    name: 'userInfo',
    data() {
        return {
            list: []
        }
    },
    mounted(){
        this.$http.get('/userInfos')
            .then((res)=> {
                this.list=[]
                res.data.forEach(element => {
                    this.list.push(element);
                });
            })
            .catch((err) =>{
                console.log(err)
            })
    }
}
</script>
    
<style scoped>
*{
    margin: 0;
    padding: 0;
}
.mainRightContentRoot {
    width: 1000px;
    height: 1000px;
}

.mainRightContentRoot .table {
    width: 100%;
}

