<template>
    <div class="transInfoRoot">




        <el-dialog title="筛选" :visible.sync="dialogFilterVisible">
            <el-form :inline="true" label-width="80px" :model="filterData">
                <el-form-item label="姓名">
                    <el-input v-model="filterData.RealName"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmitFilter">确定</el-button>
                    <el-button type="primary" @click="onCancleFilter">取消</el-button>
                </el-form-item>
            </el-form>
        </el-dialog>



        <div class="pageContainer">
            <el-pagination background layout="total, sizes, prev, pager, next" :current-page="tablePage.pageNum"
                :page-size="tablePage.pageSize" :page-sizes="pageSizes" :total="tablePage.total"
                @size-change="handleSizeChange" @current-change="handlePageChange" />
            <el-button type="primary" @click="filterDefined">筛选</el-button>
        </div>



        <!-- :default-sort="{ prop: 'leaveStationTime', order: 'ascending' }" -->
        <el-table stripe class="elTable" :header-cell-style="{ 'text-align': 'center' }"
             ref="singleTable" :data="list" highlight-current-row>


            <el-table-column property="UserName" label="用户名" width="120" align="center"></el-table-column>
            <el-table-column property="RealName" label="姓名" width="120" align="center"></el-table-column>
            <el-table-column property="content" label="操作内容" width="200" align="center"></el-table-column>
            <el-table-column property="operationTime" label="操作时间" width="200" align="center"></el-table-column>

        </el-table>

    </div>
</template>
    
<script>
export default {
    name: 'operationInfo',
    data() {
        return {
            dialogFilterVisible: false,
            filterData: {
                RealName: ''
            },


            oragionList: [],
            list: [],
            dialogFormVisible: false,
            currentSeleted: {},
            selctedItemUpdate: {

            },
            names: [],
            days: [],
            tablePage: {
                pageNum: 1, // 第几页
                pageSize: 10, // 每页多少条
                total: 0 // 总记录数
            },
            pageSizes: [10, 20, 30]
        }
    },
    methods: {
        filterDefined() {
            this.dialogFilterVisible = true;
        },
        onSubmitFilter() {
            this.dialogFilterVisible = false;
            this.list = this.getPageList(this.oragionList, this.tablePage.pageNum, this.tablePage.pageSize)
        },
        onCancleFilter() {
            this.dialogFilterVisible = false;
            // this.filterData.RealName = ''
            // this.filterData.startDay = ''
        },
        /**
     * 分页数据处理
     * @param data [Array] 需要分页的数据
     * @param num [Number] 当前第几页
     * @param size [Number] 每页显示多少条
*/
        getPageList(data, num, size) {

            //先进行筛选，再进行分页
            let result = data


            if (this.filterData.RealName != '') {
                result = result.filter(item => {
                    if (item.RealName === this.filterData.RealName)
                        return true;
                })
            }

            this.tablePage.total = result.length;


            let list, total, start, end, isFirst, isLast
            total = result.length
            isFirst = total < size
            isLast = Math.ceil(total / size) === num
            start = (num - 1) * size
            end = isFirst || isLast ? start + (total % size) : start + size
            list = result.slice(start, end)
            list.forEach((item, index) => {
                item.seq = index + start
            })
            return list
        },
        handlePageChange(currentPage) {
            this.tablePage.pageNum = currentPage
            // 在此刷新数据
            this.list = this.getPageList(this.oragionList, this.tablePage.pageNum, this.tablePage.pageSize)
        },
        handleSizeChange(pageSize) {
            this.tablePage.pageSize = pageSize
            // 在此刷新数据
            this.list = this.getPageList(this.oragionList, this.tablePage.pageNum, this.tablePage.pageSize)
        },
        getList() {
            let info = {
                collectionName: 'operationInfo',
                content: {

                }
            }
            this.$http.post('/getInfo',info)
                .then((res) => {
                    this.oragionList = res.data
                    console.log(this.oragionList)
                    this.list = this.getPageList(this.oragionList, this.tablePage.pageNum, this.tablePage.pageSize)
                })
                .catch((err) => {
                    console.log(err)
                })
        },
    },
    mounted() {
        this.getList();
    }
}
</script>
    
<style scoped>
.pageContainer {
    margin: 10px;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
}
</style>
    