<template>
    <div>
        <el-table :header-cell-style="{ 'text-align': 'center' }" ref="singleTable" :data="list" highlight-current-row>
            <el-table-column property="CarNumber" label="车牌号" width="120" align="center"></el-table-column>
            <el-table-column property="StartLocation" label="起运地" width="120" align="center"></el-table-column>
            <el-table-column property="DestinatLocation" label="目的地" align="center"></el-table-column>
            <el-table-column property="RealFangliang" label="方量" align="center"></el-table-column>
            <el-table-column property="comment" label="备注" width="200" align="center"></el-table-column>
            <el-table-column property="RealName" label="真实姓名" align="center"></el-table-column>
            <el-table-column property="UserName" label="用户名" width="200" align="center"></el-table-column>

            <el-table-column property="CarCount" label="车次" align="center"></el-table-column>

            <el-table-column property="leaveStationTime" label="出站时间" width="200" align="center"></el-table-column>
            <el-table-column property="arrivalTime" label="到工地时间" width="200" align="center"></el-table-column>
            <el-table-column property="startOffTime" label="卸货时间" width="200" align="center"></el-table-column>
            <el-table-column property="offCompleteTime" label="卸完时间" width="200" align="center"></el-table-column>
            <el-table-column property="sayStationTime" label="回站时间" width="200" align="center"></el-table-column>
            
        </el-table>
    </div>
</template>
    
<script>
export default {
    name: 'transState',
    data() {
        return {
            list: []
        }
    },
    mounted() {
        this.$http.get('/transStates')
            .then((res) => {
                this.list = []
                res.data.forEach(element => {
                    this.list.push(element);
                });
            })
            .catch((err) => {
                console.log(err)
            })
    }
}
</script>
    
<style scoped>
* {
    margin: 0;
    padding: 0;
}

.mainRightContentRoot {
    width: 1000px;
    height: 1000px;
}

.mainRightContentRoot .table {
    width: 100%;
}

/* .mainRightContentRoot .table tr {
} */

.mainRightContentRoot .table td {
    text-align: center;
}
</style>
    