<template>
  <div class="mainRoot">
    <el-menu default-active="1" class="el-menu-vertical-demo menu" background-color="#545c64" text-color="#fff"
      active-text-color="#ffd04b">
      <el-menu-item index="6" @click="menuCLick('userInfo')" align="center">
        <span slot="title">用户信息</span>
      </el-menu-item>
      <el-menu-item index="5" @click="menuCLick('transState')" align="center">
        <span slot="title">车辆状态信息</span>
      </el-menu-item>
      <el-menu-item index="4" @click="menuCLick('transInfo')" align="center">
        <span slot="title">货运明细信息</span>
      </el-menu-item>
      <el-menu-item index="7" @click="menuCLick('dicInfo')" align="center">
        <span slot="title">字典表</span>
      </el-menu-item>
      <el-menu-item index="8" @click="menuCLick('operationInfo')" align="center">
        <span slot="title">操作列表</span>
      </el-menu-item>
    </el-menu>
    <div class="rightContainer">
      <router-view></router-view>
    </div>
  </div>

</template>
  
<script>
export default {
  name: 'MainLeftMenu',
  props: {
  },
  methods: {
    menuCLick(type) {
      switch (type) {
        case 'userInfo':
          this.$router.push({
            name: 'user',
          })
          break;
        case 'transInfo':
          this.$router.push({
            name: 'transInfo',
          })
          break;
        case 'transState':
          this.$router.push({
            name: 'transState',
          })
          break;
        case 'dicInfo':
          this.$router.push({
            name: 'dicInfo',
          })
          break;
        case 'operationInfo':
          this.$router.push({
            name: 'operationInfo',
          })
          break;
      }
    }
  }
}
</script>
  
<style scoped>
.mainRoot {
  display: flex;
  width: 100vw;
}

.menu {
  height: 100vh;
  width: 15vw;
}

.rightContainer {
  height: 100vh;
}
</style>
  