<template>
    <div>
        <el-table :header-cell-style="{ 'text-align': 'center' }" ref="singleTable" :data="list" highlight-current-row>
            <el-table-column property="type" label="类型" width="120" align="center"></el-table-column>
            <el-table-column property="content" label="值" width="120" align="center"></el-table-column>
        </el-table>
    </div>
</template>
    
<script>
export default {
    name: 'dicInfo',
    data() {
        return {
            list: []
        }
    },
    mounted(){
        this.$http.get('/dicInfos')
            .then((res)=> {
                this.list=[]
                res.data.forEach(element => {
                    this.list.push(element);
                });
            })
            .catch((err) =>{
                console.log(err)
            })
    }
}
</script>
    
<style scoped>
*{
    margin: 0;
    padding: 0;
}
.mainRightContentRoot {
    width: 1000px;
    height: 1000px;
}

.mainRightContentRoot .table {
    width: 100%;
}

